import React, {useState} from "react";
import Wrapper from "../common/core/Wrapper";
import Menu from "./Menu";
import Row from "react-bootstrap/Row";
import styles from './style.module.css';
import Header from "./Header";

// DEV
import avatarSrc from './images/adam.jpg';
import withOnImagesLoadedRender from "../common/hocs/withOnImagesLoadedRender";
const user = {name: 'Adam', surname: 'Smith', avatarSrc};

const HEADER_HEIGHT = 80;
const LEFT_SIDE_WIDTH = 320;

const BOARD_ITEMS = new Map([
  ['Templates', null],
  ['Documents', null],
  ['Products', null],
  ['Services', null],
  ['My business', null],
  ['Partners', null],
  ['Competitors', null],
]);

function Dashboard() {
  const [tab, setTab] = useState(0);

  return (
    <Wrapper className={styles.wrapper}>
      <Header height={HEADER_HEIGHT} leftSideWidth={LEFT_SIDE_WIDTH}/>
      <Row className={styles.main}>
        <Menu
          user={user}
          width={LEFT_SIDE_WIDTH}
          items={BOARD_ITEMS}
          activeTabIndex={tab}
          setTab={setTab}
        />
      </Row>
    </Wrapper>
  );
}

export default withOnImagesLoadedRender(Dashboard);
